import React from 'react';
import Traakki from "./images/traakki.jpg" ;
import { Router, Link } from '@reach/router'
import Provider from './providers/Provider'
import AppWrapper from './components/AppWrapper'
import Gallery from './components/Gallery'
import FullApp from './components/App'
import Upload from './components/Upload'
import NotFound from './components/common/NotFound'
import Login from './components/Login'
import './App.css';
import { makeStyles } from '@material-ui/styles'
import Img from 'react-image'

const useStyles = makeStyles({
  root: {
    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
    border: 0,
    borderRadius: 3,
    boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
    color: 'white',
    height: 48,
    padding: '0 30px',
  },
});



function Landing() {
  const classes = useStyles()
  return (
    <>
      <div className="container center-text">
        <h1>Galeria de los 60 de Lister en Tenerife 2020</h1>
        <h1>Kuvagalleria Lister 60 Tenerife 2020</h1>
      </div>
      <Img src={Traakki} alt="Traakki" />
      <div>
        <Link  className={classes.root} to="/app/login/">
          Login
        </Link>
      </div>
    </>
  )
}

function App() {
  return (
    <Provider>
    <AppWrapper>
      <Router>
        <Landing path="/" component={Landing} />
        <FullApp path="/app/" component={FullApp} />
        <Login path="/app/login/" component={Login} />
        <Gallery path="/app/gallery/" component={Gallery} />
        <Upload path="/app/gallery/upload/" component={Upload} />
        <NotFound default component={NotFound} />
      </Router>
    </AppWrapper>
    </Provider>
  );
}

export default App;
