import React from 'react'
import Header from '../theme/Header'
import './layout.css'

export default ({ children, isLoggedIn, logout }) => {

  return (
    <>
      <Header
        siteTitle={`Gallery App`}
        isLoggedIn={isLoggedIn}
        logout={logout}
      />
      <main>{children}</main>
    </>
  )
}
